import { Component, OnInit } from '@angular/core';

import { HomeserviceService } from '../service/homeservice.service';
import { Url } from 'url';
import { AuthenticationService } from '../service/authentication.service';
import { Router } from '@angular/router';
@Component({
  selector: 'app-dms-bulletin',
  templateUrl: './dms-bulletin.component.html',
  styleUrls: ['./dms-bulletin.component.css']
})
export class DmsBulletinComponent implements OnInit {

  constructor(
    private homeservice: HomeserviceService,
    private router: Router,
    public loginService: AuthenticationService) { }

  ngOnInit(): void {
    this.getDmsBulletin();
  }
  heading: string;
  date: string;
  textMsg: string;
  path: Url;
  dmsBulletin: any;
  async getDmsBulletin() {

    this.dmsBulletin = await this.homeservice.dmsBulletin();
    console.log("dmsBulletin: " + this.dmsBulletin[0]);
    this.heading = this.dmsBulletin[0].fileName;
    this.date = this.dmsBulletin[0].creationDate;
    this.textMsg = this.dmsBulletin[0].txtArea;
  }

  showDoc(fileName: string, textMsg: string, createdDt: string) {
    console.log('list: ' + fileName + textMsg + createdDt);
    this.heading = fileName;
    this.date = createdDt;
    this.textMsg = textMsg;
  }
  selectedIndex: number;
  listClick(index: number) {
    this.selectedIndex = index;
  }
  isHomeRoute() {
    this.loginService.userType() == 'Maruti' ? this.router.navigate(['/marutiHome'])
      : this.router.navigate(['/home'])
  }
}
