import { FormControl, Validators } from "@angular/forms";
import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../service/authentication.service';
import { Router } from '@angular/router';
import { logoutUrl } from 'src/environments/environment';
@Component({
  selector: 'app-resetpassword',
  templateUrl: './resetpassword.component.html',
  styleUrls: ['./resetpassword.component.css']
})
export class ResetpasswordComponent implements OnInit {
  logout=logoutUrl;
  passwordpattern = "(?=\\D*\\d)(?=[^a-z]*[a-z])(?=[^A-Z]*[A-Z]).{9,30}";
  nameFormControl = new FormControl("", [
    Validators.required,
    Validators.minLength(9)
  ]);
  nameFormControl1 = new FormControl("", [
    Validators.required,
    Validators.minLength(9),
    Validators.pattern(this.passwordpattern)
  ]);
  nameFormControl2 = new FormControl("", [
    Validators.required,
    Validators.minLength(9)
  ]);

  

  constructor(private router: Router, private authservice: AuthenticationService) { }
  userID = '';
  log = [];
  msg: string = null;
  ngOnInit() {
    this.userID = localStorage.getItem('userId');
  }

  async resetPassword(){
    console.log("Cool");
    let userID = this.userID;
    let oldPass = this.nameFormControl.value;
    let newPass = this.nameFormControl2.value;
    console.log(userID+".."+oldPass+".."+newPass);
    this.log = await this.authservice.resetPassword(userID,oldPass,newPass);
    console.log(this.log);
    this.msg = this.log['message'];
    console.log("this.log['message']",this.log['message']);
    if(this.log['status'] ==="Y"){
      alert(this.msg);
       window.open(this.logout,"_self");
    }else{
      alert(this.msg);
      window.location.reload();
    }
  }

}
