import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../service/authentication.service';
import { baseUrlDefault, uiLink } from 'src/environments/environment';

@Component({
  selector: 'app-login-by-email',
  templateUrl: './login-by-email.component.html',
  styleUrls: ['./login-by-email.component.css']
})
export class LoginByEmailComponent implements OnInit {

  constructor(public authservice: AuthenticationService) { }

  mainLink: String;
  user: any;
  alertMsg: string;


  ngOnInit() {
    this.mainLink = uiLink;
    this.checkUser();
  }



  goToForgotPwd() {
    const url = `${baseUrlDefault}/forgotModule/#/forgotpwd?type=change`;
    window.location.href = url;
  }

  async checkUser() {
    this.user = (await this.authservice.login('', '')) as any;
    this.alertMsg = this.user.passwordExpiryDays === 30 && this.user.passwordChangeStatus === 'N' ? 'First time login. Please reset your password' : this.user.passwordExpiryDays === 50 && this.user.passwordChangeStatus === 'N' || 'Y' ? 'Your passord is expired. Pls reset.' : '';

  }

}
