import { Component, OnInit } from "@angular/core";
import { FormControl, FormGroup, Validators } from "@angular/forms";
import { ApproveRejectSoftLockUsersReq } from "../model/ApproveRejectSoftLockUsersReq";
import { SoftLockUserDetails } from "../model/SoftLockUserDetails";
import { SoftLockUserResp } from "../model/SoftLockUserResp";
import { SoflockService } from "../service/soflock.service";
import {
  MatSnackBar,
  MatSnackBarHorizontalPosition,
  MatSnackBarVerticalPosition,
} from "@angular/material/snack-bar";
@Component({
  selector: "app-softlock-unmap",
  templateUrl: "./softlock-unmap.component.html",
  styleUrls: ["./softlock-unmap.component.css"],
})
export class SoftlockUnmapComponent implements OnInit {
  useridpatternappreject = "[a-zA-Z0-9.-_]+";

  constructor(
    private softlockService: SoflockService,
    private _snackBar: MatSnackBar
  ) {}

  ngOnInit(): void {
    this.aruserSLReqList = []; //reset req list
    this.responseMsg = "";
    this.showSuccMsg = false;
    this.showFailMsg = false;
    this.is0Pendingrec = false;
    this.showMsgFor0Rec = "";
    this.getsoftlockResp = [];
    this.approveRejectForm();
  }
  arFormGroup: FormGroup;
  // @ViewChild('alert', { static: true }) alert: ElementRef;

  approveRejectForm() {
    this.arFormGroup = new FormGroup({
      userId: new FormControl("", [
        Validators.maxLength(20),
        Validators.pattern(this.useridpatternappreject),
      ]),
      dealerMapCode: new FormControl(),
      approveOrReject: new FormControl(),
      parentGroup: new FormControl(),
      locCode: new FormControl(),
      comments: new FormControl(),
    });
  }
  getsoftlockResp: SoftLockUserDetails[];
  is0Pendingrec = false;
  showMsgFor0Rec = "";
  getSoftLockUsers(userId, dealerMapcd) {
    this.finalRespmsg = "";
    this.responseMsg = "";
    this.showSuccMsg = false;
    this.showFailMsg = false;
    if (userId == "") {
      userId = null;
    }
    if (dealerMapcd == "") {
      dealerMapcd = null;
    }
    this.softlockService
      .getSoftLockUsersForUnMap(userId, dealerMapcd)
      .subscribe((resp) => {
        this.getsoftlockResp = resp.softLockUserDetails;
        if (
          this.getsoftlockResp == null ||
          (this.getsoftlockResp != null && this.getsoftlockResp.length == 0)
        ) {
          this.is0Pendingrec = true;
          this.showMsgFor0Rec = "There are no pending records";
        }
      });
  }

  updateData(event: any, userid, comment) {
    if (this.getsoftlockResp != null && this.getsoftlockResp.length > 0) {
      this.getsoftlockResp.forEach((d) => {
        if (event.value == "unMap" && d.userId == userid) {
          d.approveOrReject = "unMap";
          d.comments = comment;
        }
      });
    }
  }

  aruserSLReqList: ApproveRejectSoftLockUsersReq[] = [];
  horizontalPosition: MatSnackBarHorizontalPosition = "end";
  verticalPosition: MatSnackBarVerticalPosition = "top";
  showSuccMsg = false;
  showFailMsg = false;
  responseMsg: string;
  finalRespmsg = "";

  approveRejectUsers() {
    this.finalRespmsg = "";
    this.responseMsg = "";
    this.showSuccMsg = false;
    this.showFailMsg = false;
    this.is0Pendingrec = false;
    if (this.getsoftlockResp != null && this.getsoftlockResp.length > 0) {
      this.getsoftlockResp.forEach((sud) => {
        if (sud.approveOrReject == "unMap") {
          this.aruserSLReqList.push({
            userId: sud.userId,
            decision: "unMap",
            comments: sud.comments,
          });
        }
      });
    }
    this.responseMsg = "DMS secure Key Request has been [ ";
    console.log("request json to send ", this.aruserSLReqList);
    if (this.aruserSLReqList != null && this.aruserSLReqList.length > 0) {
      this.softlockService
        .unMapUsers(this.aruserSLReqList, "ADMIN")
        .subscribe((res) => {
          if (res.message.indexOf("Success") != -1) {
            this.showSuccMsg = true;
            // alert(this.aruserSLReqList[0].userId+' '+this.aruserSLReqList[0].decision+' '+res.message);
            if (
              this.aruserSLReqList != null &&
              this.aruserSLReqList.length == 1
            ) {
              this.responseMsg =
                "DMS secure Key Request has been " +
                this.aruserSLReqList[0].decision +
                " for " +
                this.aruserSLReqList[0].userId;
            } else if (
              this.aruserSLReqList != null &&
              this.aruserSLReqList.length > 1
            ) {
              this.aruserSLReqList.forEach((r) => {
                this.finalRespmsg +=
                  " " + r.decision + "  for: " + r.userId + ", ";
              });
              this.responseMsg += this.finalRespmsg + "  ] successfully";
            }

            this.aruserSLReqList = [];
            this.getsoftlockResp = [];
          }
          if (res.message.indexOf("Error Occured") != -1) {
            this.showFailMsg = true;
            //alert(this.aruserSLReqList[0].userId+' '+this.aruserSLReqList[0].decision+' '+res.message);
            this.responseMsg = JSON.stringify(res);

            this.aruserSLReqList = [];
            this.getsoftlockResp = [];
          }
          this.arFormGroup.reset();
        });
    }
    if (
      this.aruserSLReqList != null &&
      this.aruserSLReqList.length == 0 &&
      this.getsoftlockResp != null &&
      this.getsoftlockResp.length > 0
    ) {
      alert("please select record to submit");
    }
    if (
      this.aruserSLReqList != null &&
      this.aruserSLReqList.length == 0 &&
      this.getsoftlockResp != null &&
      this.getsoftlockResp.length == 0
    ) {
      alert("No record to submit");
    }
  }

  closeAlert() {
    this.showSuccMsg = false;
    this.showFailMsg = false;
    this.finalRespmsg = "";
    this.is0Pendingrec = false;
    this.showMsgFor0Rec = "";
  }
}
