import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { AuthenticationService } from "../service/authentication.service";
import { CookieService } from "ngx-cookie-service";
import { logoutUrl } from "src/environments/environment";
import { SoflockService } from "../service/soflock.service";
@Component({
  selector: "app-login",
  templateUrl: "./login.component.html",
  styleUrls: ["./login.component.css"],
})
export class LoginComponent implements OnInit {
  //formGroup: FormGroup;
  invalidLogin = false;
  uname: string;
  pwd: string;
  loginFlag: boolean;
  loading = false;
  constructor(
    private router: Router,
    private loginservice: AuthenticationService,
    private softLock: SoflockService,
    private cookieService: CookieService
  ) { }

  initForm() { }

  user: any;
  oamUserId: any;
  async checkLogin() {
    localStorage.clear(); //to be added
    console.log("Start checkLogin");

    let username = this.uname;
    let password = this.pwd;

    try {
      console.log("in checkLogin", username, password);
      this.user = (await this.loginservice.login(username, password)) as any;

      console.log("user " + JSON.stringify(this.user));
      if (!this.user.userId) {
        this.logout();
      }
      let checkSoftLock = false;
      if (this.user.leaseLine) {
        console.log("in lease line");
        checkSoftLock = true;
      } else {
        console.log("not in lease line");
        checkSoftLock = await this.checkSoftLock();
        this.user = (await this.loginservice.login(username, password)) as any;
      }

      if (this.user != null && this.user.hasOwnProperty("userName")) {
        // sessionStorage.setItem('username', username);
        for (var prop in this.user) {
          if (prop != "password") {
            localStorage.setItem(prop, this.user[prop]);

            // this.cookieService.set(prop, this.user[prop]);
          }
          if (prop == "firstName") {
            this.loginservice.firstName =
              this.user[prop] &&
              this.user[prop][0].toUpperCase() +
              this.user[prop].substr(1).toLowerCase();
          }
          if (prop == "lasrName") {
            this.loginservice.lastName =
              this.user[prop] &&
              this.user[prop][0].toUpperCase() +
              this.user[prop].substr(1).toLowerCase();
          }
          if (prop == "pmc") {
            this.loginservice.pmc = this.user[prop];
            //localStorage.setItem('pmc', this.loginservice.pmc);
          }

          if (prop == "userName") {
            console.log("in login service: ", this.user[prop]);
            this.loginservice.userName = this.user[prop];
          }
        }
        // console.log();
        this.loginservice.userName =
          this.loginservice.firstName + " " + this.loginservice.lastName;
        localStorage.setItem("userName", this.loginservice.userName);

        if (localStorage.getItem("errorCode") === "L") {
          this.alertMsg = "Your Account is Locked! Kindly contact DMS Support.";
          this.alertFlag = "block";
          this.invalidLogin = true;
          this.loading = false;
          this.autoCloseAlertBox();
        } else if (localStorage.getItem("errorCode") === "I") {
          // alert("User is Inactive! Kindly contact with Admin.");
          this.alertMsg = "User is Inactive! Kindly contact DMS Support.";
          this.alertFlag = "block";
          this.invalidLogin = true;
          this.loading = false;
          this.autoCloseAlertBox();
        } else if (localStorage.getItem("errorCode") === "IP") {
          //alert("Kindly access via valid Lease Line / Dongle.");
          this.alertMsg =
            "User Access is not authorized over this IP Address on Lease Line.";
          this.alertFlag = "block";
          this.invalidLogin = true;
          this.loading = false;
          this.autoCloseAlertBox();
        } else if (localStorage.getItem("errorCode") === "V") {
          // alert("Not a valid User! Kindly try again.");
          this.alertMsg =
            "Incorrect User Id or Password! Kindly enter correct details.";
          this.alertFlag = "block";
          this.invalidLogin = true;
          this.loading = false;
          this.autoCloseAlertBox();
        } else if (
          localStorage.getItem("errorCode") === "IDK" ||
          !checkSoftLock
        ) {
          this.alertMsg = "User Access is not authorized over this Soft Lock.";
          this.alertFlag = "block";
          this.invalidLogin = true;
          this.loading = false;
          this.autoCloseAlertBox();
          //alert("Invalid dongle key id! Kindly try again.");
        } else if (
          localStorage.getItem("errorCode") === "DKNA" &&
          !checkSoftLock
        ) {
          // alert("Dongle key not available! Kindly try again.");
          this.alertMsg = "Soft Lock not found! Kindly contact DMS Support.";
          this.alertFlag = "block";
          this.invalidLogin = true;
          this.loading = false;
          this.autoCloseAlertBox();
        } else if (localStorage.getItem("errorCode") === "VIPR") {
          this.alertMsg = "User Access is not authorized over this IP Address.";
          this.alertFlag = "block";
          this.invalidLogin = true;
          this.loading = false;
          this.autoCloseAlertBox();
        } else if (localStorage.getItem("errorCode") === "DL") {
          this.alertMsg = "User ID is deleted ! Kindly contact DMS Support.";
          this.alertFlag = "block";
          this.invalidLogin = true;
          this.loading = false;
          this.autoCloseAlertBox();
        } else {
          // if (localStorage.getItem('fstTimeChangePass') === 'Y') {
          //   this.router.navigate(['/resetpassword']);
          // } else {
          this.loading = false;
          if (
            localStorage.getItem("userName").toLocaleUpperCase() === "NOC MSIL"
          ) {

            this.router.navigate(["/userManagement"]);
          } else {
            if (localStorage.getItem("loginType") === "Maruti") {
              this.router.navigate(["/marutiHome"]);
            } else if (localStorage.getItem("loginType") === "Phili") {
              this.router.navigate(["/philimarutiHome"]);
            } else {
              this.router.navigate(["/home"]);
            }
          }
          // }
          this.invalidLogin = false;
        }
      } else {
        // alert("Not a valid User! Kindly try again.");
        this.alertMsg = "Not a valid User! Kindly try again.";
        this.alertFlag = "block";
        this.invalidLogin = true;
        this.loading = false;
        this.autoCloseAlertBox();
      }
    } catch {
      this.alertMsg = "Not a valid User! Kindly try again.";
      this.alertFlag = "block";
      this.invalidLogin = true;
      this.loading = false;
      this.autoCloseAlertBox();
    }
  }

  alertMsgPass: string = null;
  private resetPassTimeoutId: any = null;
  docViewPassAlert: boolean = false;

  ngOnInit() {
    //this.initForm();
    this.loginFlag = false;
    this.checkLogin();
    // this.getIP();
    // console.log('in init: ', this.loginservice.ipAddress);
    this.loading = true;

  }

  resetPassAlert() {
    const messages: { [key: number]: string } = {
      3: "3 days left to change your password.",
      2: "2 days left to change your password.",
      1: "1 day left to change your password.",
      0: "Your password has expired. Please change your password.",
    };
    this.loginservice.getResetPassKey().subscribe((res) => {
      // Clear previous timer if any
      if (this.resetPassTimeoutId) {
        clearTimeout(this.resetPassTimeoutId);
        this.resetPassTimeoutId = null;
      }

      if (res !== undefined && messages.hasOwnProperty(res)) {
        this.alertMsgPass = messages[res];
        this.docViewPassAlert = false;
        this.alertFlag = "block";

        if (res === 0) {
          this.resetPassTimeoutId = setTimeout(() => {
            this.onClosePassAlert(0);
          }, 5000);
        }
      } else {
        this.checkLogin();
      }
    });
  }

  async checkSoftLock() {
    try {
      const getValidationUrl: any = await this.softLock.getValidationUrl(
        (this.user as any).userId
      );
      if (getValidationUrl.status == "1") {
        const validateUrl = getValidationUrl.validationURL;
        const validationCheck = await this.softLock.validate(validateUrl);
        return validationCheck;
      } else if (getValidationUrl.status == "0") {
        return true;
      }
    } catch {
      return false;
    }
  }

  logout() {
    localStorage.clear();
    sessionStorage.clear();
    window.open(logoutUrl, "_self");
  }
  async autoCloseAlertBox() {
    // await new Promise(resolve => setTimeout(resolve, 10000));
    setTimeout(() => {
      this.alertFlag = "none";
      this.onCloseAlert();
    }, 10000);
  }

  alertMsg: string;
  alertFlag: string = "none";
  docView: boolean;
  onCloseAlert() {
    this.alertFlag = "none";
    this.alertMsg = null;
    this.logout();
  }

  buildValidationUserId(userId) {
    let random = this.generateRandomString(40);
    const decodedUserId = random + userId + "40";
    return btoa(decodedUserId);
  }

  generateRandomString(length = 40) {
    const chars = "ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
    let result = "";
    for (let i = 0; i < length; i++) {
      result += chars.charAt(Math.floor(Math.random() * chars.length));
    }
    return result;
  }

  getIP() {
    this.loginservice.getIPAddress().subscribe((res: any) => {
      this.loginservice.ipAddress = res.ip;
      console.log("res: ", this.loginservice.ipAddress);
    });
    console.log("ip: ", this.loginservice.ipAddress);
  }


  onClosePassAlert(res?: number) {
    this.alertFlag = "none";
    this.alertMsgPass = null;

    if (this.resetPassTimeoutId) {
      clearTimeout(this.resetPassTimeoutId);
      this.resetPassTimeoutId = null;
    }

    if (res === 0) {
      this.router.navigate(["/forgotpwd"]);
    } else {
      this.checkLogin();
    }
  }

} 
