import { Component, OnInit } from '@angular/core';
import { HomeserviceService } from '../service/homeservice.service';
import { Url } from 'url';
import { AuthenticationService } from '../service/authentication.service';
import { pptDirectoryUrl, docUrl } from '../../environments/environment';
import { Router } from '@angular/router';
@Component({
  selector: 'app-best-practice',
  templateUrl: './best-practice.component.html',
  styleUrls: ['./best-practice.component.css'],
})
export class BestPracticeComponent implements OnInit {
  viewer = 'google';
  selectedType = 'pptx';
  constructor(
    private homeservice: HomeserviceService,
    private router: Router,
    public loginService: AuthenticationService
  ) {}

  pptUrl = pptDirectoryUrl;
  docUrl = docUrl;
  ngOnInit(): void {
    this.getbestPractice();
  }
  bestPractice: any;
  heading: string;
  date: string;
  path: Url;
  imgList = [];
  async getbestPractice() {
    this.bestPractice = await this.homeservice.bestPractice();
    //let finalPath =this.bestPractice[0].imgName.split('.').slice(0, -1)+"/" + this.bestPractice[0].imgName.split('.').slice(0, -1);
    let imageCont = this.bestPractice[0]?.imgCount;
    for (let i = 0; i < imageCont; i++) {
      // let imgPath = { path: this.pptUrl+""+this.bestPractice[i].fileName+"/"+this.bestPractice[i].fileName+i+".jpg" };
      let imgPath = {
        path:
          this.pptUrl +
          '' +
          this.bestPractice[0].fileName +
          '/' +
          this.bestPractice[0].fileName +
          i +
          '.jpg',
      };
      this.imgList.push(imgPath);
    }
    this.heading = this.bestPractice[0].fileName;
    this.date = this.bestPractice[0].creationDate;
  }

  showDoc(lst) {
    this.imgList.length = 0;
    //let finalPath =lst.imgName.split('.').slice(0, -1)+"/" + lst.imgName.split('.').slice(0, -1);

    for (let j = 0; j < lst.imgCount; j++) {
      let imgPath = {
        path: this.pptUrl + '' + lst.fileName + '/' + lst.fileName + j + '.jpg',
      };
      this.imgList.push(imgPath);
    }

    this.heading = lst.fileName;
    this.date = lst.creationDate;
  }
  selectedIndex: number;
  listClick(index: number) {
    this.selectedIndex = index;
  }
  isHomeRoute() {
    //console.log('homeeeii',localStorage.getItem('usertype'))
    this.loginService.userType() == 'Maruti'
      ? this.router.navigate(['/marutiHome'])
      : this.router.navigate(['/home']);
  }
}
