import { Component, OnInit } from '@angular/core';
import { FormControl, Validators } from "@angular/forms";
import { AuthenticationService } from '../service/authentication.service';

@Component({
  selector: 'app-forgotid',
  templateUrl: './forgotid.component.html',
  styleUrls: ['./forgotid.component.css']
})
export class ForgotidComponent implements OnInit {

  nameFormControl = new FormControl("", [
    Validators.required,
    Validators.email
  ]);
  faildScreen: boolean = false;
  sucessScreen: boolean = false;
  fristScreen: boolean = true;
  otpAlert: boolean = false;
  constructor(private authservice: AuthenticationService) { }

  userID = '';
  log = [];
  msg: string = null;

  ngOnInit(): void {
  }

  async forgotUserID(){
    let emailID = this.nameFormControl.value;
    console.log(emailID);
    this.log = await this.authservice.forgotID(emailID);
    this.msg = this.log['message'];
    console.log('this.msg: ',this.msg);
    if(this.msg=="Your User ID sent to the email ID please check"){
      this.fristScreen = false;
      //this.faildScreen = true;
      //this.otpAlert = false;
      this.sucessScreen = true;
    }else{
      //this.fristScreen = false;
      this.faildScreen = true;
      this.otpAlert = false;
     // this.sucessScreen = true;
    }
  }

  goContinue() {
    let emailID = this.nameFormControl.value;
    console.log(emailID);
    this.fristScreen = false;
    //this.faildScreen = true;
    //this.otpAlert = false;
    this.sucessScreen = true;
  }
}
