import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { baseUrl_slock } from "src/environments/environment";
import { SoftLockResponse } from "../model/SoftLockResponse";
import { SoftLockUserResp } from "../model/SoftLockUserResp";

@Injectable({
  providedIn: "root",
})
export class SoflockService {
  constructor(private http: HttpClient) {}
  url = baseUrl_slock;

  validate(url): Promise<boolean> {
    return new Promise((resolve, reject) => {
      this.http.get<string>(url, { responseType: "text" as "json" }).subscribe(
        (data) => {
          if (data.includes("Data Synced")) {
            resolve(true);
          } else {
            resolve(false);
          }
        },
        (error) => {
          reject(error);
        }
      );
    });
  }

  getSoftLockUsers(userId, dealerMapCode) {
    return this.http.get<SoftLockUserResp>(
      this.url + "getSoftLockUsers/" + userId + "/" + dealerMapCode,
      {}
    );
  }
  getSoftLockUsersForUnMap(userId, dealerMapCode) {
    return this.http.get<SoftLockUserResp>(
      this.url + "getSoftLockUsersUnMap/" + userId + "/" + dealerMapCode,
      {}
    );
  }
  getValidationUrl(userId) {
    return new Promise((resolve, reject) => {
      this.http
        .post<string>(this.url + "getValidationURL", {
          userId,
          machineKey: "",
          clientRandomNo: "",
        })
        .subscribe(
          (data) => {
            resolve(data);
          },
          (error) => {
            reject(error);
          }
        );
    });
  }
  approveRejectUsers(arUserList, loggedInUser) {
    return this.http.post<SoftLockResponse>(
      this.url + "approveUsers/" + loggedInUser,
      arUserList,
      {}
    );
  }
  unMapUsers(arUserList, loggedInUser) {
    return this.http.post<SoftLockResponse>(
      this.url + "unMapUsers/" + loggedInUser,
      arUserList,
      {}
    );
  }
}
