  dmsBulletin() {
    let lst = localStorage.getItem("departmentList");
    let pmc: any;
    let dealerCatagory;
    if (this.loginService.pmc != null || this.loginService.pmc != "") {
      //pmc = this.loginService.pmc;
      pmc = localStorage.getItem("pmc");
      dealerCatagory = localStorage.getItem("dealerCategory");
      console.log("pmc: ", pmc);
      //localStorage.setItem('pmc', this.loginService.pmc);
    } else {
      pmc = localStorage.getItem("pmc");
      dealerCatagory = localStorage.getItem("dealerCategory");

      console.log("pmc: ", pmc);
    }
    let reson = localStorage.getItem("reson");
    let chennal = localStorage.getItem("channel");
    let userType = localStorage.getItem("userType");
    const params = new HttpParams({})
      .set("dept", lst)
      .set("pmc", pmc)
      .set("reson", reson)
      .set("chennal", chennal)
      .set("userType", userType)
      .set("dealerCatagory", dealerCatagory);

    console.log("params: " + params.getAll("dept"));
    return this.http
      .get<Document[]>(this.url + "dmsBulletin", { params })
      .toPromise();
  }

