  deleteMultipleDocuments(idArray: any): Observable<HttpEvent<any>> {
    let params = new HttpParams();
    params = params.append("idArray", idArray.join(", "));
    const req = new HttpRequest(
      "POST",
      `${this.url}deleteMultipleDocuments`,
      params,
      {
        reportProgress: true,
        responseType: "json",
      }
    );
    return this.http.request(req);
  }

