  search() {
    if (this.searchcategory == "Notice") {
      this.docTable = false;
      this.bulletinTable = true;
    } else if (this.searchcategory == "Document_Repository") {
      this.docTable = true;
      this.bulletinTable = false;
    } else if (this.searchcategory == "Best_Practices") {
      this.docTable = true;
      this.bulletinTable = false;
    } else {
     // alert('Category is a mandatory field !');
      this.alertMsg="Category is a mandatory field !";
  this.alertFlag='block';
  
    }
    this.idArray = [];
    let pmc:any ;
    if(this.loginService.pmc !=null || this.loginService.pmc!=""){
      pmc = localStorage.getItem('pmc');
      console.log('pmc: ', pmc);
      
      //localStorage.setItem('pmc', this.loginService.pmc);
    }else{
      pmc = localStorage.getItem('pmc');
      console.log('pmc: ', pmc);
    }
    this.service.ItemsArray=null;
    const formData: FormData = new FormData();
    formData.append('category', this.searchcategory);
    if(this.searchdept!=null && this.searchdept!=undefined && this.searchdept=='ALL'){
      var alldepts="ALL,Sales,Service,Spares";
      console.log('this.searchdept is ',this.searchdept);
    // formData.append('dept', this.searchdept);
    formData.append('dept', alldepts);    
    }else{
      console.log('this.searchdept is ',this.searchdept);
      formData.append('dept', this.searchdept);
    }
    formData.append('viewBy', this.searchviewby);
    formData.append('channel', this.searchchannel);
    formData.append('region', this.searchregion);
    formData.append('pmc', pmc);
    formData.append('subCategory', this.searchSubcategory);
    console.log('searchSubcategory before sending to api ',this.searchSubcategory);
    this.service.sendSearchRequest(formData)
      .subscribe(response => {
        this.ItemsArray = response.Success;
        this.service.ItemsArray=this.ItemsArray;
        console.log(this.ItemsArray);
        console.log('in service Itemarray',this.service.ItemsArray);
        if (this.ItemsArray == undefined) {
         // alert("No Document found for Search Criteria");
          this.alertMsg="No Document found for Search Criteria.";
          this.alertFlag='block';
        }
      });
  }


