  onSelectUploadCategory(value: any) {
    console.log("Value: " + value);
    this.uploadcategory = value;
    if (this.uploadcategory == "Notice") {
      this.inputType = "text";
      this.fileDiv = false;
      this.textDiv = true;
      this.isUploadSubcategoryVisible=false;
    } else if (this.uploadcategory == "Document_Repository") {
      this.inputType = "application/pdf";
      this.fileDiv = true;
      this.textDiv = false;
      this.isUploadSubcategoryVisible=true;
    } else {
      this.inputType = ".ppt,.pptx";
      this.fileDiv = true;
      this.textDiv = false;
      this.isUploadSubcategoryVisible=false;
    }
  }
