  checkNull(type: string) {    
    
    if (this.uploadcategory == null || this.uploadcategory == "") {
      // alert("Kindly Choose Department");
       this.alertMsg="Kindly Choose Category";
       this.alertFlag='block';
       return false;
     }
    if (this.uploaddept == null || this.uploaddept == "") {
     // alert("Kindly Choose Department");
      this.alertMsg="Kindly Choose Department.";
      this.alertFlag='block';
      return false;
    }
    if (this.uploadregion == null || this.uploadregion == "") {
      //alert("Kindly Choose Region");
      this.alertMsg="Kindly Choose Region.";
      this.alertFlag='block';
      return false;
    }
    if (this.uploadviewby == null || this.uploadviewby == "") {
      //alert("Kindly Choose View By");
      this.alertMsg="Kindly Choose View By.";
      this.alertFlag='block';
      return false;
    }
    if (this.uploadchannel == null || this.uploadchannel == "") {
      //alert("Kindly Choose Channel");
      this.alertMsg="Kindly Choose Channel.";
      this.alertFlag='block';
      return false;
    }
    

    if (type == 'upload') {      
      if (this.selectedFiles == null) {
        //alert("Kindly Choose File");
        this.alertMsg="Kindly Choose File.";
      this.alertFlag='block';
        return false;
      }
      if (this.uploadrole == null || this.uploadrole == "") {
        //alert("Kindly Choose Role");
        this.alertMsg="Kindly Choose Role.";
      this.alertFlag='block';
        return false;
      }
    } else {
      if (this.selectedtextfield == null || this.selectedtextfield == "") {
        //alert("Kindly Insert Text");
        this.alertMsg="Kindly Insert Text.";
      this.alertFlag='block';
        return false;
      }
      if (this.selectedtitle == null || this.selectedtitle == "") {
        //alert("Kindly insert Title");
        this.alertMsg="Kindly insert Title.";
      this.alertFlag='block';
        return false;
      }
    }

    return true;
  }

  //Upload PDF and PPT 
  upload() {
    
    if (this.checkNull('upload')) {
      this.showOverlay = true;
      console.log("In Upload Section: ",this.showOverlay);
      //this.currentFile = this.selectedFiles.item(0);
      this.currentFile = this.selectedFiles;
      console.log("this.currentFile",this.currentFile);
      let pmc:any;
      if(this.loginService.pmc !=null || this.loginService.pmc!=""){
        //pmc = this.loginService.pmc;
      pmc = localStorage.getItem('pmc');
      console.log('pmc: ', pmc);
        //localStorage.setItem('pmc', this.loginService.pmc);
      }else{
        pmc = localStorage.getItem('pmc');
      console.log('pmc: ', pmc);
      }
      var FileSize = this.currentFile.size / 1024 / 1024; // in MB
      if (FileSize > 40) {
        //alert('File size exceeds 5 MB');
        this.alertMsg="File size exceeds 40 MB.";
     this.alertFlag='block';
      } else {
        const formData: FormData = new FormData();
        formData.append('file', this.currentFile);
        formData.append('category', this.uploadcategory);
        formData.append('loggedinuser', this.userDisplay);
        formData.append('dept', this.uploaddept);
        formData.append('viewBy', this.uploadviewby);
        formData.append('channel', this.uploadchannel);
        formData.append('region', this.uploadregion);
        formData.append('role', this.uploadrole);
        formData.append('pmc', pmc);
        formData.append('subCategory', this.uploadSubcategory);
        
        console.log('Form Data: ');
        this.http.post<any>(`${this.url}uploadFile`, formData).subscribe(
          (response) => {            
            alert(response.message)
          });
           //this.alertFlag='block';
        if (formData != null) {
          this.showOverlay = false;
          console.log("In Upload Section: ",this.showOverlay);
          this.displaySave = 'none';

        }
      }
    }
    
    // alert('Data Save Successfully');
    //this.displaySave='none';
  }

