  //delete Multiple Rows Event
  deleteMultiFlag: boolean;
  deletePopUp() {
    if (this.idArray == null || this.idArray == '') {
     // alert('Please Select Document first.');
    }else{
    this.deleteMultiFlag = true;
    this.display = 'block';}
    this.selectAll=false;
  }

  idArray: any = [];
  selectAll:any;
  deleteMultiple() {
    
    console.log('this.idArray: ' + this.idArray);
    if (this.idArray == null || this.idArray == '') {
      //alert('Please Search document.');
      this.alertMsg="Please Search document.";
      this.alertFlag='block';
    }else{
      let params = new HttpParams();
      params = params.append('idArray', this.idArray.join(', '));
      this.http.post<any>(`${this.url}/deleteMultipleDocuments`, params).subscribe(
        (response) => response.message);
      this.display = 'none';
      
      
    }
    this.selectAll=false;
    this.idArray == null;
    this.deleteMultiFlag = false;
    this.search();
//window.location.reload();
  }


  //Push and Pop Checked Values in Array
  itemselected: Amfile;
  getCheckboxValues(ev, itemselected) {
    if (ev.target.checked) {
      if (this.idArray.indexOf(itemselected.id) == -1) {
        this.idArray.push(itemselected.id);
      }
    } else {
      let removeIndex = this.idArray.indexOf(itemselected.id);
      if (removeIndex !== -1)
        this.idArray.pop(removeIndex, 1);
    }
  }


  //CheckUnCheckMultipleRows
  checkUncheckAll(eve) {
    if (eve.target.checked) {
      console.log('eve.target.checked:');
      for (let j = 0; j < this.service.ItemsArray.length; j++) {
        this.service.ItemsArray[j].checked = true;
        console.log('this.service.ItemsArray.length',this.service.ItemsArray.length);
        this.idArray.push(this.service.ItemsArray[j].id);
        console.log('this.idArray',this.idArray);
        this.display = 'none';
        this.dialog.closeAll();

        //window.location.reload();
       // this.search();
      }
    } else {
      for (let j = 0; j < this.service.ItemsArray.length; j++) {
        this.service.ItemsArray[j].checked = false;
      }
      this.idArray = [];
      this.display = 'none';
      this.dialog.closeAll();
      //window.location.reload();
      this.search();
    }
  }

