  checkSaveEditValues(type: String) {
    if (this.editdept == null || this.editdept == "") {
     //alert("Kindly Choose Department");
      this.alertMsg="Kindly Choose Department.";
      this.alertFlag='block';
      return false;
    }
    if (this.editregion == null || this.editregion == "") {
      //alert("Kindly Choose Region");
      this.alertMsg="Kindly Choose Region.";
      this.alertFlag='block';
      return false;
    }
    if (this.editviewby == null || this.editviewby == "") {
      //alert("Kindly Choose View By");
      this.alertMsg="Kindly Choose View By.";
      this.alertFlag='block';
      return false;
    }
    if (this.editchannel == null || this.editchannel == "") {
      //alert("Kindly Choose Channel");
      this.alertMsg="Kindly Choose Channel.";
      this.alertFlag='block';
      return false;
    }

    if (type == 'doc') {

      if ((this.searchcategory!==null && this.searchcategory!=="" && this.searchcategory=="Document_Repository" )&& (this.editSubcategory == null || this.editSubcategory == "")) {
        //alert("Kindly Choose Role");
        this.alertMsg="Kindly Choose Sub Category.";
      this.alertFlag='block';
        return false;
      }
      if (this.editrole == null || this.editrole == "") {
        //alert("Kindly Choose Role");
        this.alertMsg="Kindly Choose Role.";
      this.alertFlag='block';
        return false;
      }
     
    } else if (type == 'bulletin') {
      if (this.editedtextfield == null || this.editedtextfield == "") {
        //alert("Kindly Insert Text");
        this.alertMsg="Kindly Insert Text.";
      this.alertFlag='block';
        return false;
      }
      if (this.editedtitle == null || this.editedtitle == "") {
        //alert("Kindly insert Title");
        this.alertMsg="Kindly insert Title.";
      this.alertFlag='block';
        return false;
      }
    }
    return true;
  }
