import { Component, OnInit } from "@angular/core";
import {
  AbstractControl,
  FormControl,
  ValidationErrors,
  ValidatorFn,
  Validators,
} from "@angular/forms";
import { AuthenticationService } from "../service/authentication.service";
import { uiLink } from "../../environments/environment";
import { MatSnackBar, MatSnackBarHorizontalPosition, MatSnackBarVerticalPosition } from "@angular/material/snack-bar";
import { ActivatedRoute } from "@angular/router";
@Component({
  selector: "app-forgotpwd",
  templateUrl: "./forgotpwd.component.html",
  styleUrls: ["./forgotpwd.component.css"],
})
export class ForgotpwdComponent implements OnInit {
  user: any;
  mainLink: String;
  nameFormControl = new FormControl("", [
    Validators.required,
    Validators.minLength(4),
    Validators.maxLength(100),
    Validators.pattern(/^[a-zA-Z0-9.]+$/),
  ]);
  nameFormControl1 = new FormControl("", [Validators.required]);
  nameFormControl2 = new FormControl("", [Validators.required]);
  nameFormControl3 = new FormControl("", [Validators.required]);
  nameFormControl4 = new FormControl("", [Validators.required, Validators.maxLength(15), this.strongPasswordValidator()]);
  nameFormControl5 = new FormControl("", [Validators.required, Validators.maxLength(15)]);
  secondScreen: boolean = false;
  fristScreen: boolean = true;
  otpAlert: boolean = false;
  userAlert: boolean = false;
  thardScreen: boolean = false;
  wrongCrit: boolean = false;
  finalScreen: boolean = false;
  hederText: string = "FORGOT PASSWORD?";
  constructor(public authservice: AuthenticationService, private _snackBar: MatSnackBar, private route: ActivatedRoute) { }
  horizontalPosition: MatSnackBarHorizontalPosition = 'center';
  verticalPosition: MatSnackBarVerticalPosition = 'top';

  ngOnInit() {
    this.mainLink = uiLink;

    const type = this.route.snapshot.queryParams['type'];
    this.hederText = type === 'change' ? 'CHANGE PASSWORD' : 'FORGOT PASSWORD';

  }

  log = [];
  msg: string = null;

  async sendMail() {
    let userID = this.nameFormControl.value;
    this.log = await this.authservice.forgotPassword(userID);
    this.msg = this.log["message"];
    if (this.msg != "") {
      this.otpAlert = false;
    }
  }

  isFormValid(): boolean {
    return (
      this.nameFormControl4.valid &&
      this.nameFormControl5.valid &&
      this.nameFormControl4.value === this.nameFormControl5.value
    );
  }

  strongPasswordValidator(): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      const password = control.value;

      if (!password) return null;

      const errors: any = {};

      if (this.nameFormControl3.value && password.toLowerCase().includes(this.nameFormControl3.value.toLowerCase())) {
        errors.containsUsername = "Password cannot contain the username";
      }
      if (password.length < 10) {
        errors.minLength = "Password must be at least 9 characters long";
      }

      // At least one uppercase letter
      if (!/[A-Z]/.test(password)) {
        errors.uppercase =
          "Password must contain at least one uppercase letter [A-Z]";
      }

      // At least one lowercase letter
      if (!/[a-z]/.test(password)) {
        errors.lowercase =
          "Password must contain at least one lowercase letter [a-z]";
      }

      // At least one number
      if (!/[0-9]/.test(password)) {
        errors.number = "Password must contain at least one number [0-9]";
      }

      // At least one special character
      if (!/[!@#$%^&*(),.?":{}|<>]/.test(password)) {
        errors.specialChar =
          "Password must contain at least one special character [!@#$%^&*]";
      }

      return Object.keys(errors).length > 0 ? errors : null;
    };
  }
  resend() {
    this.alertMsg = "OTP sent again. Please check your Mail for OTP !";
    this.alertFlag = "block";
    //alert("OTP sent again. Please check your Mail for OTP !");
    this.sendMail();
  }
  async checkId() {
    let userID = this.nameFormControl.value;
    this.authservice.userIdForgot = userID;
    if (userID == "") {
      this.userAlert = true;
    } else {
      this.userAlert = false;
    }
    this.log = await this.authservice.checkUserId(userID);
    this.msg = this.log["msg"];
    if (this.msg == "Y") {
      this.fristScreen = false;
      this.secondScreen = true;
      this.otpAlert = false;
      this.sendMail();
    } else {
      this.userAlert = false;
      this.alertMsg = "Invalid user Id, Please check and try again.";
      this.alertFlag = "block";

      this.secondScreen = false;
    }
  }
  async goVerifyOTP() {
    let userName = this.nameFormControl1.value;
    let otp = this.nameFormControl2.value;
    this.log = await this.authservice.verifyOTP(userName, otp);
    this.msg = this.log["status"];
    if (this.msg == "Y") {
      this.fristScreen = false;
      this.secondScreen = false;
      //this.otpAlert = true;
      this.thardScreen = true;
    } else {
      this.otpAlert = true;
    }
  }
  async changePassword() {
    let user = this.nameFormControl3.value;
    let newPass = this.nameFormControl4.value;
    let cnfPass = this.nameFormControl5.value;
    if (newPass == cnfPass) {
      this.log = await this.authservice.changePass(user, newPass);
      this.fristScreen = false;
      this.secondScreen = false;
      this.thardScreen = false;
      //this.wrongCrit = true;
      this.finalScreen = true;
    } else {
      this.wrongCrit = true;
    }
  }
  openLink() {
    window.open(uiLink, "_self");
  }
  alertMsg: string;
  alertFlag: string = "none";
  docView: boolean;
  onCloseAlert() {
    this.alertFlag = "none";
    this.alertMsg = null;
  }
}

