import { NgModule, Component } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { LoginComponent } from "./login/login.component";
import { HomeComponent } from "./home/home.component";
import { LogoutComponent } from "./logout/logout.component";
import { ForgotpwdComponent } from "./forgotpwd/forgotpwd.component";
import { ForgotidComponent } from "./forgotid/forgotid.component";
import { ResetpasswordComponent } from "./resetpassword/resetpassword.component";
import { ChangepasswordComponent } from "./changepassword/changepassword.component";
import { MarutiHomeComponent } from "./maruti-home/maruti-home.component";
import { DocumentRepositoryComponent } from "./document-repository/document-repository.component";
import { BestPracticeComponent } from "./best-practice/best-practice.component";
import { DmsBulletinComponent } from "./dms-bulletin/dms-bulletin.component";
import { MarutiSearchDocumentComponent } from "./maruti-search-document/maruti-search-document.component";
import { SuzukiSearchDocumentComponent } from "./suzuki-search-document/suzuki-search-document.component";
import { UserManagementComponent } from "./user-management/user-management.component";
import { ApproveRejectComponent } from "./softlock/approve-reject.component";
import { PhiliHomeComponent } from "./phili-home/phili-home.component";
import { SoftlockUnmapComponent } from "./softlock-unmap/softlock-unmap.component";
import { userManagementGuard } from "./guards/user-management.gaurd";
import { MarutiSearchDocument } from "./guards/maruti-search-docs.gaurd";
import { LoginByEmailComponent } from "./login-by-email/login-by-email.component";
import { ModuleTypeGuard } from './guards/module-type.guard';


const routes: Routes = [
  {
    path: "",
    redirectTo: "/login",
    pathMatch: "full",
  },
  {
    path: "login",
    component: LoginComponent,
    canActivate: [ModuleTypeGuard],

  },
  {
    path: "home",
    component: HomeComponent,
    canActivate: [ModuleTypeGuard],

  },
  {
    path: "logout",
    component: LogoutComponent,
    canActivate: [ModuleTypeGuard],

  },
  {
    path: "forgotpwd",
    component: ForgotpwdComponent,
    canActivate: [ModuleTypeGuard],
  },
  {
    path: "forgotid",
    component: ForgotidComponent,
    canActivate: [ModuleTypeGuard],
  },
  {
    path: "resetpassword",
    component: ResetpasswordComponent,
    canActivate: [ModuleTypeGuard],
  },
  {
    path: "changepassword",
    component: ChangepasswordComponent,
    canActivate: [ModuleTypeGuard],
  },
  {
    path: "marutiHome",
    component: MarutiHomeComponent,
    canActivate: [ModuleTypeGuard],
  },
  {
    path: "docRepository",
    component: DocumentRepositoryComponent,
    canActivate: [ModuleTypeGuard],
  },
  {
    path: "dmsBulletin",
    component: DmsBulletinComponent,
    canActivate: [ModuleTypeGuard],
  },
  {
    path: "bestPractice",
    component: BestPracticeComponent,
    canActivate: [ModuleTypeGuard],
  },
  {
    path: "marutiSearchDocument",
    canActivate: [MarutiSearchDocument, ModuleTypeGuard],
    component: MarutiSearchDocumentComponent,

  },
  {
    path: "suzukiSearchDocument",
    component: SuzukiSearchDocumentComponent,
    canActivate: [ModuleTypeGuard],
  },
  {
    path: "userManagement",
    canActivate: [userManagementGuard, ModuleTypeGuard],
    component: UserManagementComponent,
  },
  {
    path: "softlock",
    component: ApproveRejectComponent,
    canActivate: [ModuleTypeGuard],
  },
  {
    path: "softlock-unmap",
    component: SoftlockUnmapComponent,
    canActivate: [ModuleTypeGuard],
  },
  {
    path: "philimarutiHome",
    component: PhiliHomeComponent,
    canActivate: [ModuleTypeGuard],
  },
  {
    path: "login-by-email",
    component: LoginByEmailComponent,
    canActivate: [ModuleTypeGuard],
  }
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, {
      scrollPositionRestoration: "top",
      useHash: false,
    }),
  ],
  exports: [RouterModule],
})
export class AppRoutingModule { }
