import { Injectable } from '@angular/core';
import { HttpClient, HttpRequest, HttpHeaders, HttpEvent,HttpParams } from '@angular/common/http';
import { baseUrl_uam } from '../../environments/environment';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UserManagementService {
  

  constructor(private http:HttpClient) { }
  url = baseUrl_uam;

  getUserTypes() {
    return this.http.get<String[]>(this.url+"getUserTypes", { }).toPromise();
  }

  getLocCodeList(userType) {
    
    // return this.http.get<String[]>(`${this.url}+"getLocCodeList?userType=`+userType);
    return this.http.get<String[]>(this.url+"getLocCodeList?userType="+userType, {});
  }

  getEmployeeCodeList(parentGroup,mulDealerCd,locCode,userTypeSelected){
    return this.http.get<Map<String,String>>(this.url+"getEmpCodeList?parentGroup="+parentGroup+"&mulDealerCode="+mulDealerCd+"&locCode="+locCode+"&userType="+userTypeSelected, {});
  }
  getUserDesignations(selectedmulUserLocCd, principalMapCd) {
    return this.http.get<String[]>(this.url+"getDesignations?locCode="+selectedmulUserLocCd+"&principalMapCode="+principalMapCd, {});
  }

  getChannelList(principalMapCd) {
    return this.http.get<String[]>(this.url+"getChannels?principalMapCode="+principalMapCd, {});
  }

  isExistedUser(userId){
    return this.http.get(this.url+"isUserExisted?userId="+userId, { responseType: 'text' });
  }

  createUser(userData){
    return this.http.post<any>(this.url+"createUser", userData,  { responseType: 'text' as 'json'  });


  }

  getParentGroup(){
    return this.http.get<String[]>(this.url+"getParentGroup", {}).toPromise();
  }

  getMulDealerCodeList(pg){
    return this.http.get<String[]>(this.url+"getMulDealerCodes?parentGroup="+pg, {});
  }
  
  getDLRLocationCodes(pg,muldlrcode){
    return this.http.get<String[]>(this.url+"getDealerLocCodes?parentGroup="+pg+"&mulDealerCode="+muldlrcode, {});
  }

  getCompanyCode(pg, muldlrcode, loccd){
    return this.http.get(this.url+"getCompanyCode?parentGroup="+pg+"&mulDealerCode="+muldlrcode+"&locCode="+loccd, {responseType: 'text'});
  }
  getDealerMapCode(pg, muldlrcode, loccd,ut){
    return this.http.get(this.url+"getDealerMapCode?parentGroup="+pg+"&mulDealerCode="+muldlrcode+"&locCode="+loccd+"&userType="+ut, {responseType: 'text'});
  }
  getRoles(){
    return this.http.get<Map<String,String>>(this.url+"getRoles", {});
  }

  getFunctions(pg,muldlrCd,comcd,lc){
    return this.http.get<any>(this.url+"getFunctions?parentGroup="+pg+"&mulDealerCode="+muldlrCd+"&companyCode="+comcd+"&locCode="+lc, {});
  }

  
  createDlrUser(userData){
    return this.http.post<any>(this.url+"createDlrUser", userData,  { responseType: 'text' as 'json'  });
  }
  deleteUserBasedonId(userType,userId){
    return this.http.put<any>(this.url+"deleteUser/"+userType+"/"+userId, {},{responseType: 'text' as 'json'});
        
  }
  getUserInfoForDelete(userId){
    return this.http.get(this.url+"getUserInfo/"+userId, {responseType: 'text'});
  }

  getRegionDataForMul(mapId){
    return this.http.get(this.url+`getRegionDescAndRegionCd/${mapId}`, {})
  }
}
