import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../service/authentication.service';
import { logoutUrl } from 'src/environments/environment';
import { Router } from '@angular/router';
@Component({
  selector: 'app-maruti-header',
  templateUrl: './maruti-header.component.html',
  styleUrls: ['./maruti-header.component.css']
})
export class MarutiHeaderComponent implements OnInit {

  constructor(private router: Router,public loginService:AuthenticationService) { }
  
  userDisplay = '';
  
  ngOnInit() {
   
    if(this.loginService.userName!=null){
      this.userDisplay=this.loginService.userName;
      localStorage.setItem('userName',this.userDisplay);
    
    } else{
      this.loginService.userName= localStorage.getItem('userName');
    }
     /* this.userDisplay = localStorage.getItem('userName');
console.log('in login method befor condition: ',this.userDisplay);
     if(this.userDisplay =='' || this.userDisplay ==null){
       console.log('in login method after if this.userDisplay == || this.userDisplay ==null: ',this.userDisplay);
      this.userDisplay=this.loginService.userName;
    } */
  }

  logout(){
  localStorage.clear();
  sessionStorage.clear();
  window.open(logoutUrl,"_self");
}

  displaySave: any;
  firstName: string;
  lastName: string;
  emailId: string;
  mobNo: string;
  empCd: string;
  dealerMapCd: string;
  mess: any;
  mspin:string;
  designation:string;
  docView: boolean = false;
  detailsList:any;
  dlrUser:boolean=false;
  mulUser:boolean=false;
  getUserProfile() {
    this.dlrUser=false;
  this.mulUser=false;
  console.log("localStorage" + localStorage.getItem('userId'));
    this.loginService.getUserProfile(localStorage.getItem('userId')).subscribe(result => {
      this.detailsList = result
      this.firstName = this.detailsList.fristName;
      this.lastName = this.detailsList.lastName;
      this.emailId = this.detailsList.emailId;
      this.mobNo = this.detailsList.mobNo;
      this.empCd = this.detailsList.empCd;
      this.dealerMapCd = this.detailsList.dealerMapCd;
      this.mspin=this.detailsList.mispn;
      this.designation=this.detailsList.designation;

      console.log("firstName" + this.detailsList.fristName);

      if(localStorage.getItem('userType')=="MUL"){
        this.dlrUser=false;
        this.mulUser=true;
        console.log("mul ",this.mulUser);
        
      }else{
        this.dlrUser=true;
        this.mulUser=false;
        console.log("dealer ",this.dlrUser);
        
      }
    });
  }
  resetData() {
    //window.location.reload();
  }
  update() {
    let first = this.firstName;
    let last = this.lastName;
    let email = this.emailId;
    let mob = this.mobNo;
    if ((first == "" || first == undefined) || (last == "" || last == undefined)
      || (email == "" || email == undefined) || (mob == "" || mob == undefined)) {
      var mess = "Please enter";
      if (first == "" || first == undefined) {
        mess = mess + "/frist name";
      }
      if (last == "" || last == undefined) {
        mess = mess + "/ last name";
      }
      if (email == "" || email == undefined) {
        mess = mess + "/ email";
      }
      if (mob == "" || mob == undefined) {
        mess = mess + "/ mobile";
      }
      alert(mess)
    } else {
      console.log(first + "," + last + "," + email + "," + mob)
      this.loginService.getUpdateUserProfile(first, last, email, mob, localStorage.getItem('userId'), this.empCd, this.dealerMapCd).subscribe(result => {
        this.mess = result.status
        window.location.reload();
        this.docView = true;
        alert(this.mess)
        console.log("Response: " + this.mess)
      });
    }
  }
  emailPattern = "[a-zA-Z0-9.-_]{1,}@[a-zA-Z.-]{2,}[.]{1}[a-zA-Z]{2,}";
  validateEmailId(event: any) {
    console.log("Mob   =" + event.target.value)
  }
  mobNumberPattern = "[6789][0-9]{9}";
  keyPress(event: any) {

    const pattern = /[6789][0-9]{9}/;
    console.log("Mob   =" + event.target.value)
    if ((event.target.value).length == 10) {
      if (!pattern.test((event.target.value))) {
        event.preventDefault();
      }
    } else if ((event.target.value).length > 10) {
      event.preventDefault();
    }
  }
  
}
