import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { baseUrl } from 'src/environments/environment';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  url= baseUrl;

  constructor( private http:HttpClient,) { }


  getMulEmpCode(){

    return this.http.get<any>(this.url+"getMulLocCode");
  }

 storeUserActivitylogs(userId : string ,linkClicked : string){
    return this.http.get<any>(this.url+"logs/"+userId+"/"+linkClicked,{responseType:'text' as 'json'});
  }

  isNexaDealer(pmc : string){
    return this.http.get<any>(this.url+"dealerCheck/"+pmc,{responseType:'text' as 'json'});
  }

}
