import { Injectable } from '@angular/core';
import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';
import { moduleType } from '../../environments/environment';

@Injectable({
    providedIn: 'root'
})
export class ModuleTypeGuard implements CanActivate {

    constructor(private router: Router) { }

    canActivate(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): boolean {

        const url = (state.url || '').split('?')[0];

        if (moduleType === 'forgot') {

            const allowedForgotRoutes = ['/forgotpwd', '/forgotid'];

            if (allowedForgotRoutes.includes(url)) {
                return true;
            }

            this.router.navigate(['/forgotpwd']);
            return false;
        }

        if (moduleType === 'portal') {

            const blockInPortal = ['/forgotpwd', '/forgotid'];

            if (blockInPortal.includes(url)) {
                this.router.navigate(['/login']);
                return false;
            }

            return true;
        }

        return true;
    }
}
