import { User } from '../model/User';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, from } from 'rxjs';
import { baseUrl } from '../../environments/environment';
@Injectable({
  providedIn: 'root',
})
export class AuthenticationService {
  constructor(private http: HttpClient) { }
  userName: any = null;
  firstName: string = null;
  lastName: string = null;
  pmc: string = null;
  url = baseUrl;
  userIdForgot: any;
  ipAddress: string;
  user = [];
  invalidLogin = true;
  public getIPAddress() {
    return this.http.get('http://api.ipify.org/?format=json');
  }
  async login(username, password) {

    //return await this.http.get<User[]>(this.url+"loginuser/"+username+"/"+password).toPromise();
    const data = await this.http.get<User[]>(this.url + 'loginuser').toPromise();

    return data;
  }

  async forgotPassword(userID) {
    return await this.http
      .get<String[]>(this.url + 'forgotpassword/' + userID)
      .toPromise();
  }
  async checkUserId(userID) {
    return await this.http
      .get<String[]>(this.url + 'checkUserId/' + userID)
      .toPromise();
  }
  async verifyOTP(userID, otp) {
    return await this.http
      .get<String[]>(this.url + 'verifyOtp/' + userID + '/' + otp)
      .toPromise();
  }

  async resetPassword(userID, oldpass, newpass) {
    return await this.http
      .get<String[]>(
        this.url + 'resetPassword/' + userID + '/' + oldpass + '/' + newpass
      )
      .toPromise();
  }
  async changePass(userID, newpass) {
    return await this.http
      .get<String[]>(this.url + 'changePassword/' + userID + '/' + newpass)
      .toPromise();
  }
  async forgotID(emailID) {
    return await this.http
      .get<String[]>(this.url + 'forgotuserid/' + emailID)
      .toPromise();
  }

  isUserLoggedIn() {
    let user = sessionStorage.getItem('username');
    //console.log(!(user === null))
    return !(user === null);
  }

  userType() {
    let userType = localStorage.getItem('loginType');
    return userType;
  }

  logOut() {
    sessionStorage.removeItem('username');
  }
  getUserProfile(usrId) {
    return this.http.get<any>(this.url + 'getUserProfileDtls/' + usrId);
  }
  getUpdateUserProfile(first, last, email, mob, userId, empCd, dealerMapCd) {
    return this.http.get<any>(
      this.url +
      'getUpdateUserProfileDtls/' +
      first +
      '/' +
      last +
      '/' +
      email +
      '/' +
      mob +
      '/' +
      userId +
      '/' +
      empCd +
      '/' +
      dealerMapCd
    );
  }

  getUserLoginKey(): Observable<string> {
    const userId = localStorage.getItem('userId');
    const pmc = localStorage.getItem('pmc');
    const url = `${this.url}loginuserkey/${userId}/${pmc}`;
    return this.http.get<string>(url, { responseType: 'text' as 'json' });
  }

  getResetPassKey() {
    const userId = localStorage.getItem('userId');
    const url = `${this.url}getResetPassKey/${userId}`;
    return this.http.get<number>(url);
  }
}
