  async checkUser() {
    this.user = (await this.authservice.login('', '')) as any;

    if (this.user.passwordExpiryDays === 30 && this.user.passwordChangeStatus === 'N') {
      this.alertMsg = 'First time login. Please reset your password';
    }
    else if (this.user.passwordExpiryDays === 50 && (this.user.passwordChangeStatus === 'N' || this.user.passwordChangeStatus === 'Y')) {
      this.alertMsg = 'Your password is expired. Please reset.';
    }
    else {
      this.alertMsg = '';
    }

    this.alertFlag = "block";
    if (this.user.passwordExpiryDays === 30 && this.user.passwordChangeStatus === 'N') {
      this.router.navigate(['/login-by-email']);
    }
    else if (this.user.passwordExpiryDays === 50 && (this.user.passwordChangeStatus === 'N' || this.user.passwordChangeStatus === 'Y')) {
      this.router.navigate(['/login-by-email']);
    }
    else if (this.user.passwordExpiryDays >= 0 && this.user.passwordExpiryDays <= 30 && this.user.passwordChangeStatus === 'Y') {
      this.router.navigate(['/userManagement']);
    }


    this.autoCloseAlertBox();
  }

  strongPasswordValidator(): ValidatorFn {
    return (control: AbstractControl): ValidationErrors | null => {
      const password = control.value;
      if (!password) return null;
      const errors: any = {};
      if (this.userTypeSelected === "DLR") {
        const dlrUserIdControl = this.userFormData.get("dlrFormGroup").get("dlruserId");
        if (dlrUserIdControl && dlrUserIdControl.value && password.toLowerCase().includes(dlrUserIdControl.value.toLowerCase())) {
          errors.containsUsername = "Password cannot contain the username";
        }


      }
      else if (this.userTypeSelected === "MUL") {
        const mulUserIdControl = this.userFormData.get("mulformGroup").get("userId");
        if (mulUserIdControl && mulUserIdControl.value && password.toLowerCase().includes(mulUserIdControl.value.toLowerCase())) {
          errors.containsUsername = "Password cannot contain the username";
        }
      }

      // Minimum length
      if (password.length < 9) {
        errors.minLength = "Password must be at least 9 characters long";
      }
      // At least one uppercase letter
      if (!/[A-Z]/.test(password)) {
        errors.uppercase = "Password must contain at least one uppercase letter [A-Z]";
      }
      // At least one lowercase letter
      if (!/[a-z]/.test(password)) {
        errors.lowercase = "Password must contain at least one lowercase letter [a-z]";
      }
      // At least one number
      if (!/[0-9]/.test(password)) {
        errors.number = "Password must contain at least one number [0-9]";
      }
      // At least one special character
      if (!/[!@#$%^&*(),.?":{}|<>]/.test(password)) {
        errors.specialChar = "Password must contain at least one special character [!@#$%^&*]";
      }
      return Object.keys(errors).length > 0 ? errors : null;
    };
  }

