  checkUserExistance(uid, userType) {
    if (userType === "MUL") {
      const mulUserIdCtrl = this.userFormData.get("mulformGroup.userId");
      if (!mulUserIdCtrl || mulUserIdCtrl.invalid) {
        return;
      }
    }

    if (userType === "DLR") {
      const dlrUserIdCtrl = this.userFormData.get("dlrFormGroup.dlruserId");
      if (!dlrUserIdCtrl || dlrUserIdCtrl.invalid) {
        return;
      }
    }

    // this.isshowmsgNotexistedUser=false;
    //check user existed in DB or not
    this.userExistStr = "";
    this.userManagementService.isExistedUser(uid).subscribe((d) => {
      this.userExistStr = d;
      this.isUserExisted = false;
      if (d.indexOf("Not existed") != -1) {
        this.userExist = true;
        this._snackBar.open("Valid User Id", "", {
          duration: 15000,
          horizontalPosition: this.horizontalPosition,
          verticalPosition: this.verticalPosition,
          panelClass: ["mat-toolbar", "mat-primary", "style-success"],
        });
      }
      if (d.indexOf("User already exists") != -1) {
        this.userExist = false;
        this._snackBar.open(`${uid}`, d, {
          duration: 15000,
          horizontalPosition: this.horizontalPosition,
          verticalPosition: this.verticalPosition,
          panelClass: ["mat-toolbar", "mat-error"],
        });
      }
      // if(d.indexOf('Not existed')!=-1){
      //   this.userExistStr='Valid User';
      // this.isshowmsgNotexistedUser=true;
      //  }
      // if(d.indexOf('User already exists')!=-1){
      //   this.userExistStr=d;
      // this.isshowmsgExistedUser=true;
      // }
    });
    // return this.userExistStr;
  }

