  is: any;
  public toggleSelection(item, _list) {
    if (_list == 1 && this.funlist1.length > 0) {
      this.funlist1.forEach((l1) => {
        if (l1.name == item.name) {
          this.is = item.name;

          l1.selected = !item.selected;
        }
      });
    }

    if (_list == 2 && this.funlist2.length > 0) {
      this.funlist2.forEach((l2) => {
        if (l2.name == item.name) {
          l2.selected = !item.selected;
        }
      });
    }
  }

  public moveSelected(direction) {
    if (direction === "left") {
      this.funlist2.forEach((item) => {
        if (item.selected == true) {
          this.funlist1.push(item);
        }
      });

      this.funlist2 = this.funlist2.filter((i) => !i.selected);
    } else {
      this.funlist1.forEach((item) => {
        if (item.selected == true) {
          this.funlist2.push(item);
        }
      });
      this.funlist1 = this.funlist1.filter((i) => !i.selected);
    }
  }

  public moveAll(direction) {
    if (direction === "left") {
      this.funlist1 = [...this.funlist1, ...this.funlist2];
      this.funlist2 = [];
    } else {
      this.funlist2 = [...this.funlist2, ...this.funlist1];
      this.funlist1 = [];
    }
  }
