  showmsgforreset = "";
  showmsgfornotexist = "";
  async resetPassword() {
    this.isresetpwd = false;
    this.isusernotexisttoreset = false;
    await this.authservice
      .changePass(
        this.userFormData.controls["resetPwdFormGroup"].value.userIDForReset,
        this.userFormData.controls["resetPwdFormGroup"].value.newPassForReset
      )
      .then((result) => {
        if (result["status"] == "Y") {
          this.isresetpwd = true;
          this.showmsgforreset = result["message"];
        } else if (result["message"].indexOf("Not existed") != -1) {
          this.isusernotexisttoreset = true;
          this.showmsgfornotexist = result["message"];
        } else if (result["status"] == "N") {
          // alert('Password has been not reset');
          this.isusernotexisttoreset = true;
          this.showmsgfornotexist = result["message"];
        }
      });
    this.userFormData.controls["resetPwdFormGroup"].reset();
    // this.resetPwdFormGroup.controls['newPassForReset'].reset();
    // this.resetPwdFormGroup.controls['confirmPasswordForReset'].reset();
  }

